import {
  Container,
  TextField,
  InputAdornment,
  IconButton,
} from "@material-ui/core";
import { DataGrid } from "@material-ui/data-grid";
import { Search as SearchIcon } from "@material-ui/icons";
import axios from "axios";
import React, { useEffect, useState } from "react";

function App() {
  const [rows, setRows] = useState([]);
  const [rowsHolder, setRowsHolder] = useState([]);
  const [loading, setLoading] = useState(false);
  const [term, setTerm] = useState("");

  const columns = [
    {
      field: "fullName",
      headerName: "ชื่อ นามสกุล",
      description: "This column has a value getter and is not sortable.",
      sortable: true,
      width: 160,
      valueGetter: (params) =>
        `${params.getValue(params.id, "name") || ""} ${
          params.getValue(params.id, "lastname") || ""
        }`,
    },

    {
      field: "phone",
      headerName: "เบอร์โทรศัพท์",
      width: 150,
    },
    {
      field: "department",
      headerName: "สาขา",
      width: 180,
    },
  ];

  useEffect(() => {
    const fetchEmployees = async () => {
      setLoading(true);
      const url =
        "https://shark-app-j9jc9.ondigitalocean.app/users/department/การตลาด";
      // const url = "http://localhost:8080/users/department/การตลาด";
      await axios.get(url).then((res) => {
        setRows(res?.data);
        setRowsHolder(res?.data);
        setLoading(false);
      });
    };
    fetchEmployees();
  }, []);

  const searchUserByName = (term) => {
    const newData = rowsHolder.filter((item) => {
      const result = item.name;
      const value = term;
      return result.indexOf(value) > -1;
    });

    setRows(newData);
  };

  const searchUserByPhone = (term) => {
    setRows([]);
    rowsHolder.map((item) => {
      if (item.phone) {
        const onlyNumbers = item.phone.replace(/\D/g, "");
        if (onlyNumbers.includes(term)) {
          // console.log(onlyNumbers);
          setRows((pre) => [...pre, { ...item }]);
        }
      }
    });
  };

  const checkSearchChar = (term) => {
    if (isNumeric(term)) {
      searchUserByPhone(term);
    } else {
      searchUserByName(term);
    }
  };

  function isNumeric(value) {
    return /^-?\d+$/.test(value);
  }

  const handleSearch = (e) => {
    setTerm(e.target.value);
    if (e.target.value) checkSearchChar(e.target.value);
    else setRows(rowsHolder);
  };

  return (
    <div>
      <Container style={{ padding: 20 }} maxWidth="lg">
        {/* <Toolbar></Toolbar> */}

        <div
          style={{
            marginTop: 24,
            marginBottom: 24,
            flexDirection: "column",
            alignItems: "center",
            display: "flex",
          }}
        >
          <div style={{ marginBottom: 30 }}>
            <h3>รายชื่อพนักงานฝ่ายขาย</h3>
          </div>

          <TextField
            id="outlined-select-currency"
            InputProps={{
              style: { fontFamily: "kanit", marginTop: 24 },
              endAdornment: (
                <InputAdornment position="start">
                  <IconButton
                    aria-label="toggle password visibility"
                    // onClick={handleClickSearch}
                    edge="end"
                  >
                    <SearchIcon />
                  </IconButton>
                </InputAdornment>
              ),
            }}
            label={
              <p style={{ fontSize: 14, fontFamily: "kanit", marginTop: 24 }}>
                ค้นหาชื่อ, เบอร์โทร
              </p>
            }
            value={term}
            variant="outlined"
            onChange={handleSearch}
            // onKeyDown={(e) => (e.key === "Enter" ? handleClickSearch() : null)}
          />
        </div>

        <div style={{ marginBottom: 10, height: 400, width: "100%" }}>
          <DataGrid
            loading={loading}
            rows={rows}
            columns={columns}
            pageSize={10}
            autoHeight
            disableSelectionOnClick
          />
        </div>
      </Container>
    </div>
  );
}

export default App;
